<template>
  <div class="home-page">
    <h1 class="title">Тестовое задание на вакансию "Веб-разработчик"</h1>
    <input v-model="address" @input="suggestions" class="input_field" type="text" placeholder="введите адрес" />
    <div v-for="hint in hints" :key="hint" @click="selectAddress(hint)" class="input_hint">{{ hint }}</div>
  </div>
</template>

<script>
import { API } from '../api/';

export default {
  name: 'HomePage',
  data () {
    return {
      msg: 'Тестов',
      address: '',
      hints: []
    }
  },
  methods: {
    async suggestions () {
      //console.log(this.address)
      if (this.address && this.address.length > 3) {
        const { data } = await API.get('/q?string='+ this.address )
        this.hints = data.hints;
      } else {
        this.hints = []
      }
    },
    selectAddress (address) {
      this.address = address
      this.hints = []
    }
  }
}
</script>

<style lang="scss" scoped>
.title {
  font-weight: normal;
  color: #333;
  font-size: 1.6em;
}
.input_field {
  max-width: 480px;
  width: 80%;
  font-size: 1.2em;
  outline: none;
  border: none;
  box-shadow: 0px 0px 55px rgba(0,0,0,.15);
  padding: 12px;
}
.input_hint {
  max-width: 480px;
  width: 80%;
  margin: auto;
  font-size: 1.2em;
  padding: 12px;
  background: #fafafa;
  text-align: left;
  cursor: pointer;
  &:hover{
    background: lightgray;
  }
}
</style>



